hr {
    padding-bottom: 40px;
}
.form {
    display: flex;
    justify-content: space-between;
}
.form__name {
    display: flex;
    flex-direction: column;
}
.form__school {
    display: flex;
    flex-direction: column;
}
.form__input {
    width: 450px;
    height: 38px;
    background: #ffffff;
    border: 1px solid #ced4da;
    border-radius: 4px;
    padding-left: 18px;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
}
.form__input::placeholder {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #abb5be;
}
.form__select {
    width: 214px;
    height: 38px;
    background: #ffffff;
    border: 1px solid #ced4da;
    border-radius: 4px;
    padding-left: 18px;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
}

.form__placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #abb5be;
}
.form__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    color: #818182;
    margin-top: 48px;
    margin-bottom: 3px;
}
hr {
    color: #dee2e6;
    height: 1px;
    max-width: 100%;
    margin-top: 40px;
    opacity: 0.2;
}
select:not(:valid) {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #abb5be;
}

@media (max-width: 756px) {
    .form {
        flex-direction: column;
    }
    .form__text {
        margin-top: 19px;
    }
    .haeder__title {
        font-weight: 700;
        font-size: 54px;
        line-height: 63px;
        margin-bottom: 14px;
    }
    .header__subtitle {
        font-size: 16px;
        line-height: 28px;
    }
    .form__input {
        width: 100%;
    }
    .form__select {
        width: 100%;
    }
}
